html, body {
  width: 100%;
  height: 100%;
}

/* #root {
  width: 100%;
  height: calc(100% - 60px);
} */

.header {
  display: flex;
  justify-content: space-around;
  align-items: center;
  color: #fff;
  height: 60px;
  background-color: #007ee5;
}

.header * {
  color: #fff;
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.choice-item {
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  opacity: 0;
  -webkit-transition: opacity .15s linear;
  transition: opacity .15s linear;
  height: 100%;
  width: 100%;
}

.choice-item--visible {
  opacity: 1;
}

.choice-item__image {
  border-radius: 10px;
  max-width: calc(100% - 20px);
  max-height: 100%;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
  -webkit-animation-iteration-count: 1;
          animation-iteration-count: 1;
}

.choice-item__image--like {
  -webkit-animation-name: like;
          animation-name: like;
}

.choice-item__image--dislike {
  -webkit-animation-name: dislike;
          animation-name: dislike;
}

@-webkit-keyframes like {
  from {
    -webkit-transform: rotate(0) translate(0, 0);
            transform: rotate(0) translate(0, 0);
  }
  to {
    -webkit-transform: rotate(45deg) translate(300%, 150%);
            transform: rotate(45deg) translate(300%, 150%);
  }
}

@keyframes like {
  from {
    -webkit-transform: rotate(0) translate(0, 0);
            transform: rotate(0) translate(0, 0);
  }
  to {
    -webkit-transform: rotate(45deg) translate(300%, 150%);
            transform: rotate(45deg) translate(300%, 150%);
  }
}

@-webkit-keyframes dislike {
  from {
    -webkit-transform: rotate(0) translate(0, 0);
            transform: rotate(0) translate(0, 0);
  }
  to {
    -webkit-transform: rotate(-45deg) translate(-300%, 150%);
            transform: rotate(-45deg) translate(-300%, 150%);
  }
}

@keyframes dislike {
  from {
    -webkit-transform: rotate(0) translate(0, 0);
            transform: rotate(0) translate(0, 0);
  }
  to {
    -webkit-transform: rotate(-45deg) translate(-300%, 150%);
            transform: rotate(-45deg) translate(-300%, 150%);
  }
}
.choice-container {
  position: relative;
  height: 100%;
  outline: none;
  display: flex;
  flex-direction: column;
  width: 100%;
  overflow: hidden;
}

.choice-container .buttons {
  display: flex;
  justify-content: center;
  align-items: center;
}

.choice-container button {
  display: flex;
  justify-content: space-around;
  margin: 10px;
  width: 100px;
  border-radius: 20px;
}

.choice-container button.like {
  align-items: end;
}

.choice-container button.dislike {
  align-items: flex-end;
}

.choice-items {
  position: relative;
  flex: 1 1 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.inventories {
  display: flex;
  align-items: stretch;
  flex: 1 1 100%;
}

.inventory-item {
  max-width: 200px;
}
.inventory-item a:hover {
  text-decoration: none;
}

.card-title {
  color: #555;
  font-size: 1.3rem;
  letter-spacing: 0;
  line-height: 2.25rem;
  font-weight: 700;
}

.card-subtitle {
  color: #555;
  font-size: 1rem;
  letter-spacing: 0;
  line-height: 1.375rem;
  font-weight: 400;
}

.card_image {
  height: 120px;
  background-size: cover;
  background-position: center;
}

.card_header {
  color: #017ee5;
}

.make-model-card {
  display: flex;
  flex-direction: column;
}
.App {
  height: 100%;
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

